import { useState, useEffect } from 'react';
import axios from 'axios';
import { makeAddressPrimary } from './api'; // Adjust the import path as needed

const AddressBook = ({ userId, token }) => {
  const [addresses, setAddresses] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [formData, setFormData] = useState({
    street: '',
    city: '',
    state: '',
    country: '',
    zip: '',
    isPrimary: false,
  });

  useEffect(() => {
    const fetchAddresses = async () => {
      const result = await fetchUserAddresses(userId);
      if (!result.error) {
        setAddresses(result);
      }
    };

    fetchAddresses();
  }, [userId]);

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value,
    });
  };

  const handleSubmit = async () => {
    try {
      await axios.post('/user/addAddress', { ...formData, userId });
      setShowForm(false);
      setFormData({
        street: '',
        city: '',
        state: '',
        country: '',
        zip: '',
        isPrimary: false,
      });
      // Refresh the addresses list
      const result = await fetchUserAddresses(userId);
      setAddresses(result);
    } catch (error) {
      console.error("Error adding address:", error);
    }
  };

  const handleMakePrimary = async (addressId) => {
    try {
      const result = await makeAddressPrimary(token, userId, addressId);
      if (!result.error) {
        const updatedAddresses = addresses.map(address =>
          address.id === addressId ? { ...address, isPrimary: true } : { ...address, isPrimary: false }
        );
        setAddresses(updatedAddresses);
      }
    } catch (error) {
      console.error("Error making address primary:", error);
    }
  };

  return (
    <div className="p-6 bg-gradient-to-r from-blue-100 to-indigo-200 min-h-screen">
      <h1 className="text-3xl font-extrabold text-indigo-600 mb-6">Address Book</h1>
      <button
        onClick={() => setShowForm(true)}
        className="mb-6 px-5 py-3 bg-gradient-to-r from-indigo-500 to-purple-500 text-white rounded-lg shadow-lg transform hover:scale-105 transition duration-300"
      >
        Add Address
      </button>
      {addresses.map((address) => (
        <div
          key={address.id}
          className="p-6 mb-6 bg-white rounded-lg shadow-md hover:shadow-lg transition-shadow duration-300 flex justify-between items-center border-l-4 border-indigo-500"
        >
          <div>
            <p className="text-lg font-semibold text-gray-700">
              {address.street}, {address.city}
            </p>
            <p className="text-gray-500">{address.state}, {address.country} - {address.zip}</p>
            <p className={`text-sm font-medium ${address.isPrimary ? 'text-green-500' : 'text-gray-400'}`}>
              {address.isPrimary ? "Primary Address" : ""}
            </p>
          </div>
          {!address.isPrimary && (
            <button
              onClick={() => handleMakePrimary(address.id)}
              className="px-4 py-2 bg-green-500 text-white rounded-lg shadow transform hover:scale-105 transition duration-300"
            >
              Make Primary
            </button>
          )}
        </div>
      ))}

      {showForm && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center">
          <div className="bg-white p-8 rounded-lg shadow-lg">
            <h2 className="text-2xl font-bold mb-4 text-indigo-600">Add New Address</h2>
            <form className="space-y-4">
              <input
                type="text"
                name="street"
                placeholder="Street"
                value={formData.street}
                onChange={handleInputChange}
                className="w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-400"
              />
              <input
                type="text"
                name="city"
                placeholder="City"
                value={formData.city}
                onChange={handleInputChange}
                className="w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-400"
              />
              <input
                type="text"
                name="state"
                placeholder="State"
                value={formData.state}
                onChange={handleInputChange}
                className="w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-400"
              />
              <input
                type="text"
                name="country"
                placeholder="Country"
                value={formData.country}
                onChange={handleInputChange}
                className="w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-400"
              />
              <input
                type="text"
                name="zip"
                placeholder="ZIP Code"
                value={formData.zip}
                onChange={handleInputChange}
                className="w-full p-3 border rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-400"
              />
              <label className="inline-flex items-center">
                <input
                  type="checkbox"
                  name="isPrimary"
                  checked={formData.isPrimary}
                  onChange={handleInputChange}
                  className="form-checkbox h-5 w-5 text-indigo-600"
                />
                <span className="ml-2 text-gray-600">Set as Primary</span>
              </label>
              <div className="flex justify-end mt-6">
                <button
                  type="button"
                  onClick={() => setShowForm(false)}
                  className="mr-4 px-4 py-2 bg-gray-400 text-white rounded-lg shadow hover:bg-gray-500 transition duration-300"
                >
                  Cancel
                </button>
                <button
                  type="button"
                  onClick={handleSubmit}
                  className="px-4 py-2 bg-indigo-500 text-white rounded-lg shadow transform hover:scale-105 transition duration-300"
                >
                  Save
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default AddressBook;
